import React, { Component } from 'react';
import { format, addDays, subYears, subDays, differenceInDays } from 'date-fns';
import _ from 'lodash';

import { FacebookLoader } from './components/loading-spinner';
import { GenericChart } from './components/chart';
import { formatDollars } from './lib/string-utils';

import { bitcoinmarkets } from './temp-data';
import styles from './app.module.css';

const COINDESK_DATE_FORMAT = 'YYYY-MM-DD';
const DEFAULT_DAYS_TO_SHOW = 90;

const trimData = (data, daysToShow = DEFAULT_DAYS_TO_SHOW) => {
  const returnData = [];
  const cutOff = subDays(new Date(), daysToShow);

  _.forEach(_.reverse(data), dataEntry => {
    if (differenceInDays(cutOff, new Date(dataEntry.x)) <= 0) {
      returnData.unshift(dataEntry);
      return true;
    }
    return false;
  });

  return returnData;
};

const getChartFormattedDate = date => format(addDays(new Date(date), 1), COINDESK_DATE_FORMAT);

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      historicalBtcPrices: [],
      isLoadingHistoricalBtcPrices: true,
    };
  }

  componentDidMount() {
    const today = format(new Date(), COINDESK_DATE_FORMAT);
    const past = format(subYears(new Date(), 2), COINDESK_DATE_FORMAT);
    const url = `https://api.coindesk.com/v1/bpi/historical/close.json?start=${past}&end=${today}`;
    fetch(url)
      .then(res => res.json())
      .then(result => {
        this.setState({
          isLoadingHistoricalBtcPrices: false,
          historicalBtcPrices: result.bpi,
        });
      });
  }

  renderCharts() {
    const { historicalBtcPrices } = this.state;

    // add one day to all x because dateFn is weird
    const chartConfig = {
      btcPrice: {
        label: 'BTC Price',
        color: 'blue',
        data: trimData(
          _.sortBy(
            _.map(historicalBtcPrices, (price, date) => ({
              x: getChartFormattedDate(date),
              y: price,
              key: 'btcPrice',
            })),
            'x'
          )
        ),
        formatValue: num => formatDollars(num),
      },
      dailyComments: {
        label: '# Daily Comments',
        color: 'brown',
        data: trimData(
          _.map(bitcoinmarkets, ({ timestamp, value }) => ({
            x: getChartFormattedDate(timestamp),
            y: value,
            key: 'dailyComments',
          }))
        ),
        formatValue: num => Math.round(num),
      },
    };

    return (
      <div className={styles.chartContainer}>
        <GenericChart
          title="BitcoinMarkets daily comments vs. BTC price"
          chartConfig={chartConfig}
        />
      </div>
    );
  }

  render() {
    const { isLoadingHistoricalBtcPrices } = this.state;

    return (
      <div className={styles.container}>
        {isLoadingHistoricalBtcPrices ? <FacebookLoader /> : this.renderCharts()}
      </div>
    );
  }
}

export default App;
