import React from 'react';
import PropTypes from 'prop-types';
import { format } from 'date-fns';
import ContainerDimensions from 'react-container-dimensions';
import _ from 'lodash';

import {
  VictoryChart,
  VictoryTheme,
  VictoryAxis,
  VictoryLine,
  VictoryScatter,
  VictoryTooltip,
  VictoryLegend,
  VictoryLabel,
  VictoryVoronoiContainer,
} from 'victory';

import styles from './chart.module.css';

const CustomTooltip = ({ datum, text, style, ...otherProps }) => (
  <VictoryTooltip
    {...otherProps}
    datum={datum}
    text={[format(new Date(datum.x), 'MMM D, YYYY'), ...text]}
    style={[{ ...style[0], fontWeight: 'bold' }, ...style]}
  />
);

CustomTooltip.defaultEvents = VictoryTooltip.defaultEvents;

CustomTooltip.propTypes = {
  datum: PropTypes.any, // eslint-disable-line
  text: PropTypes.any, // eslint-disable-line
  style: PropTypes.any, // eslint-disable-line
};

const CHART_HEIGHT = 480;
const NORMALIZATION_PADDING = 0.1;

const theme = {
  ...VictoryTheme.material,
  chart: {
    scale: { x: 'time' },
    padding: { top: 64, right: 64, bottom: 180, left: 80 },
    domain: { y: [0, 1.25] },
  },
};

const GenericChart = ({ title, chartConfig }) => {
  let maxDataPoints = 0;

  const maximaMinima = _.reduce(
    chartConfig,
    (acc, { data }, key) => {
      const maxima = Math.max(..._.map(data, d => d.y));
      const minima = Math.min(..._.map(data, d => d.y));
      acc[key] = { maxima, minima, diff: maxima - minima };

      if (_.size(data) > maxDataPoints) maxDataPoints = _.size(data);
      return acc;
    },
    {}
  );

  return (
    <div>
      <div className={styles.title}>{title}</div>
      <ContainerDimensions>
        {({ width }) => (
          <VictoryChart
            width={width}
            height={CHART_HEIGHT}
            theme={theme}
            domainPadding={{ x: width / maxDataPoints }}
            containerComponent={
              <VictoryVoronoiContainer
                voronoiDimension="x"
                voronoiBlacklist={_.map(_.keys(chartConfig), key => `${key}Line`)}
                labels={({ y, key }) => {
                  const { label, formatValue } = chartConfig[key];
                  return `${label}: ${formatValue(y)}`;
                }}
                labelComponent={<CustomTooltip cornerRadius={4} flyoutStyle={{ fill: 'white' }} />}
              />
            }
          >
            {_.map(_.keys(chartConfig), (key, index) => (
              <VictoryAxis
                key={key}
                dependentAxis
                style={{ grid: { strokeDasharray: 0 } }}
                offsetX={index ? width - 64 : 80}
                tickValues={[0, 0.25, 0.5, 0.75, 1, 1.25]}
                tickLabelComponent={
                  <VictoryLabel
                    dx={index ? 20 : 0}
                    style={{ textAnchor: index ? 'start' : 'end' }}
                  />
                }
                tickFormat={y => {
                  const { minima, diff } = maximaMinima[key];
                  return chartConfig[key].formatValue(minima + (y - NORMALIZATION_PADDING) * diff);
                }}
              />
            ))}

            <VictoryAxis
              style={{
                tickLabels: { angle: 32, padding: 12, textAnchor: 'start' },
                grid: { stroke: 'none' },
              }}
              tickFormat={(x, idx) => (idx % 4 === 0 ? format(new Date(x), 'MMM D') : '')}
            />

            {_.map(chartConfig, ({ color, data }, key) => (
              <VictoryLine
                key={key}
                name={`${key}Line`}
                data={data}
                style={{ data: { stroke: color } }}
                y={({ y }) => {
                  const { minima, diff } = maximaMinima[key];
                  return (y - minima) / diff + NORMALIZATION_PADDING;
                }}
              />
            ))}

            {_.map(chartConfig, ({ color, data }, key) => (
              <VictoryScatter
                key={key}
                data={data}
                size={(d, active) => (active ? 4 : 2)}
                style={{ data: { fill: color } }}
                y={({ y }) => {
                  const { minima, diff } = maximaMinima[key];
                  return (y - minima) / diff + NORMALIZATION_PADDING;
                }}
              />
            ))}

            <VictoryLegend
              x={width / 2 - 120}
              y={CHART_HEIGHT - 96}
              orientation="horizontal"
              gutter={12}
              borderPadding={{ top: 12, right: 16, bottom: 12, left: 16 }}
              style={{ border: { stroke: 'black' }, title: { fontSize: 20 } }}
              data={_.map(chartConfig, ({ color, label }) => ({
                name: label,
                symbol: { fill: color },
              }))}
            />
          </VictoryChart>
        )}
      </ContainerDimensions>
    </div>
  );
};

GenericChart.propTypes = {
  title: PropTypes.string.isRequired,
  chartConfig: PropTypes.shape().isRequired,
};

export default GenericChart;
